@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400;500;600;900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  /* background: rgb(8 8 8); */
}

:root {
  --font-montserrat: "Montserrat", sans-serif;

  --card-width: 204px;
  --card-width2: 170px;
  --card-width3: 150px;
  --card-width4: 140px;
}

.App {
  margin: auto;
  font-family: var(--font-montserrat);
  background: rgb(8 8 8);
}
.mv {
  background: rgb(8 8 8);
  color: white;
  font-family: var(--font-montserrat);
}

.header {
  padding: 1.2rem 0;
  position: fixed;
  width: 100%;
  top: 0;

  z-index: 2;
  color: white;
  transition: all 0.3s ease-in-out;
  transform-origin: top;
  /* margin-top: 1rem; */
}
.header.active {
  background: black;
  color: white;
}
.header__bottom {
  display: none;
}
.header__items {
  display: flex;
  justify-content: space-between;
  align-items: center;
  justify-items: center;
}

.header__items,
.header__bottom {
  list-style: none;
}
.header__items li,
.header__bottom li {
  position: relative;
  margin-right: 2rem;
  cursor: pointer;
  padding: 0.3rem;
}

.header__items li::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 2px;
  background-color: red;
  transition: transform 300ms ease-in-out;
  transform: scaleX(0);
}
.header__items li:hover::before {
  transform: scaleX(1);
}
.header__items .a,
.header__bottom .a {
  text-decoration: none;
  color: white;
}
.header__items .a.active,
.header__bottom .a.active {
  text-decoration: none;
  color: white;
}
.header h1 {
  font-weight: 400;
}

@media screen and (max-width: 630px) {
  .header__items {
    justify-content: center;
  }
  .header {
    display: block;
    text-align: center;
  }
  .header__items {
    display: none;
  }
  .header__bottom {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    position: fixed;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 0.5rem;
    background: black;
    margin: 0;
    z-index: 30;
  }
}

:root {
  --title-fontSize: 3rem;
  --title-fontSize2: 2.5rem;
  --btn-font: 0.8rem;
}

.banner {
  position: absolute;
  top: 0;
  object-fit: contain;
  height: 100%;
  width: 100%;
  z-index: -1;
}

.banner__layout {
  z-index: -1;
  position: absolute;
  background: rgb(11, 10, 8);
  background: linear-gradient(0deg,
      rgba(11, 10, 8, 1) 0%,
      rgba(5, 5, 5, 0.6589986336331408) 95%);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.banner__content {
  position: relative;
  display: flex;
  height: 90vh;
  justify-content: center;
  align-items: flex-end;
  color: white;
  /* width: 100%; */
}

.movie__poster {
  border-radius: 2rem;
}

.movie__details {
  position: relative;
  top: 0;
  padding-right: 3rem;
  display: flex;
  flex-direction: column;
}

.movie__title {
  font-size: var(--title-fontSize);
  padding-bottom: 2rem;
  font-family: var(--font-montserrat);
  max-width: 500px;
  /* font-weight: 500; */
}

.movie__overview {
  font-family: var(--font-montserrat);
  padding-bottom: 2rem;
  max-width: 550px;
  /* font-weight: 200; */
}

.btn {
  font-size: 1rem;
  font-family: var(--font-montserrat);
  /* padding: 0.4rem 0.8rem; */
  cursor: pointer;
  border: 2px solid gray;
  border-radius: 2rem;
  transition: all 0.25s;
  background: transparent;
  color: white;
}

.btn:hover {
  background: red;
  color: white;
  /* border: 1px solid white; */
}

.t {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px 10px;
  border-radius: 1.5rem;
  box-shadow: 10px 0px 6px -9px white;
}

.t::-webkit-scrollbar {
  display: none;
}

.container {
  background: rgb(8 8 8);
  color: white;
  font-family: var(--font-montserrat);
  padding: 20px 0;
}

h3 {
  font-weight: 500;
}

